import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { HomeComponent } from './home/home.component'
import { ProductComponent } from './product/product.component'
import { ProductFaqComponent } from './product-faq/product-faq.component'
import { BreadComponent } from './bread/bread.component'
import { BreadInnerComponent } from './bread-inner/bread-inner.component'
import { StoryComponent } from './story/story.component'
import { NewsComponent } from './news/news.component'
import { NewsInnerComponent } from './news-inner/news-inner.component'
import { WellnessArticleComponent } from './wellness-article/wellness-article.component'
import { WellnessCalendarComponent } from './wellness-calendar/wellness-calendar.component'
import { WellnessEventComponent } from './wellness-event/wellness-event.component'
import { WellnessTeamComponent } from './wellness-team/wellness-team.component'
import { CareersComponent } from './careers/careers.component'
import { ContactComponent } from './contact/contact.component'
import { PlantComponent } from './plant/plant.component'

const routes: Routes = [
  { path: '', component: HomeComponent, pathMatch: 'full' },
  { path: 'product', component: ProductComponent },
  { path: 'product-faq', component: ProductFaqComponent },
  { path: 'bread', component: BreadComponent },
  { path: 'bread-inner', component: BreadInnerComponent },
  { path: 'story', component: StoryComponent },
  { path: 'news', component: NewsComponent },
  { path: 'news-inner', component: NewsInnerComponent },
  { path: 'wellness-article', component: WellnessArticleComponent },
  { path: 'wellness-calendar', component: WellnessCalendarComponent },
  { path: 'wellness-event', component: WellnessEventComponent },
  { path: 'wellness-team', component: WellnessTeamComponent },
  { path: 'careers', component: CareersComponent },
  { path: 'contact', component: ContactComponent },
  { path: 'plant', component: PlantComponent },
  { path: '**', component: HomeComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
