import { Component, OnInit } from '@angular/core';
declare var $: any

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styles: [
  ]
})
export class HomeComponent implements OnInit {

  constructor() { }

  ngOnInit(): void {

    // index slider
    $('.index-slick').slick({
        arrows: false,
        autoplay: true,
        autoplaySpeed: 5000
    });

    // bread slider
    $('.bread-slick').slick({
      arrows: true,
      autoplay: false,
      slidesToShow: 1,
      // slidesToScroll: 3,
      centerMode: true,
      infinite: true,
      prevArrow: $('.bread-slider').find('.bread-back-btn'),
      nextArrow: $('.bread-slider').find('.bread-next-btn'),
      mobileFirst: true,
        responsive: [
      {
        breakpoint: 700,
        settings: {
          slidesToShow: 2
        }
      },
      {
        breakpoint: 1000,
        settings: {
          slidesToShow: 3
        }
      }
    ]
  });

    // article slider
    $('.article-slick').slick({
      arrows: true,
      autoplay: false,
      slidesToShow: 1,
      // slidesToScroll: 3,
      infinite: true,
      prevArrow: $('.article-slider').find('.btn-tab-left'),
      nextArrow: $('.article-slider').find('.btn-tab-right'),
      mobileFirst: true,
        responsive: [
      {
        breakpoint: 700,
        settings: {
          slidesToShow: 2
        }
      },
      {
        breakpoint: 1290,
        settings: {
          slidesToShow: 3
        }
      }
    ]
  });

  }

}
