import { Component, OnInit } from '@angular/core';
declare var $: any

@Component({
  selector: 'app-product',
  templateUrl: './product.component.html',
  styles: [
  ]
})
export class ProductComponent implements OnInit {

  constructor() { }

  ngOnInit(): void {

      // tab nav slider
      // products-tab
      $('.products-page').find('.tab').slick({
        arrows: true,
        autoplay: false,
        slidesToShow: 1,
        // slidesToScroll: 3,
        infinite: true,
        prevArrow: $('.tab-cont').find('.btn-tab-left'),
        nextArrow: $('.tab-cont').find('.btn-tab-right'),
        mobileFirst: true,
          responsive: [
        {
          breakpoint: 500,
          settings: {
            slidesToShow: 2
          }
        },
        {
          breakpoint: 700,
          settings: {
            slidesToShow: 3
          }
        },
        {
          breakpoint: 1000,
          settings: {
            slidesToShow: 4
          }
        },
        {
          breakpoint: 1290,
          settings: {
            slidesToShow: 6
          }
        }
      ]
    });

  // bread-tab
    $('.bread-page').find('.tab').slick({
        arrows: true,
        autoplay: false,
        slidesToShow: 1,
        // slidesToScroll: 3,
        infinite: true,
        prevArrow: $('.tab-cont').find('.btn-tab-left'),
        nextArrow: $('.tab-cont').find('.btn-tab-right'),
        mobileFirst: true,
          responsive: [
        {
          breakpoint: 500,
          settings: {
            slidesToShow: 2
          }
        },
        {
          breakpoint: 700,
          settings: {
            slidesToShow: 3
          }
        },
        {
          breakpoint: 1000,
          settings: {
            slidesToShow: 4
          }
        },
        {
          breakpoint: 1290,
          settings: {
            slidesToShow: 5
          }
        }
      ]
    });

  }

}
