import { Component, OnInit } from '@angular/core';
declare var $: any

@Component({
  selector: 'app-wellness-article',
  templateUrl: './wellness-article.component.html',
  styles: [],
})
export class WellnessArticleComponent implements OnInit {
  constructor() {}

  ngOnInit(): void {
    
    // calculate bmi
    function bmi_calculator() {
      var bmi_feet:any = $('#bmi-feet').val();
      var bmi_inch:any = $('#bmi-inch').val();

      var bmi_ht:any = bmi_feet * 12 + parseInt(bmi_inch);

      var bmi_weight_lbs:any = $('#bmi-weight').val();
      var bmi_weight_kg:any = $('#bmi-weight').val() * 2.2;

      var bmi_wt_type:any = $('#bmi-wt-type').val();

      var bmi_result:any;

      $('.bmi-result').html('');
      $('#bmi-result').hide();
      $('.bmi-height-alert').hide();
      $('.bmi-weight-alert').hide();
      $('.bmi-note').html('');

      if (bmi_feet == '' || bmi_inch == '' || bmi_weight_lbs == '') {
        if (bmi_feet == '' || bmi_inch == '') {
          $('.bmi-height-alert').html('"Height is required"');
          $('.bmi-height-alert').show();
        }
        if (bmi_weight_lbs == '') {
          $('.bmi-weight-alert').html('"Weight is required"');
          $('.bmi-weight-alert').show();
        }
      } else {
        if (bmi_wt_type == 'lbs') {
          bmi_result =
            Math.round((bmi_weight_lbs / (bmi_ht * bmi_ht)) * 703 * 100) / 100;
          bmi_result = Math.round(bmi_result);
          $('#ter-bmi').val(bmi_result);
        } else {
          bmi_result =
            Math.round((bmi_weight_kg / (bmi_ht * bmi_ht)) * 703 * 100) / 100;
          bmi_result = Math.round(bmi_result);
          $('#ter-bmi').val(bmi_result);
        }

        // console.log(bmi_result);
        $('#bmi-result').show();
        $('.bmi-result').show().html(bmi_result);

        if (bmi_result < 18.5) {
          $('.bmi-result')
            .show()
            .html(bmi_result + ' (UNDERWEIGHT) ');
        } else if (bmi_result >= 18.5 && bmi_result <= 24.9) {
          $('.bmi-result')
            .show()
            .html(bmi_result + ' (NORMAL) ');
        } else if (bmi_result >= 25 && bmi_result <= 29.9) {
          $('.bmi-result')
            .show()
            .html(bmi_result + ' (OVERWEIGHT) ');
        } else if (bmi_result >= 30) {
          $('.bmi-result')
            .show()
            .html(bmi_result + ' (OBESE) ');
        } else {
        }
      }
    }

    // calculate ter
    function ter_calculator() {
      var ter_wt = $('#ter-weight').val();
      var ter_wt_lbs = ter_wt / 2.2;
      var ter_wt_type = $('#ter-wt-type').val();
      var ter_bmi = $('#ter-bmi').val();
      var activity = $('#activity').val();
      var act_rate;
      // console.log(ter_wt);
      // console.log(ter_wt_type);
      // console.log(ter_bmi);
      // console.log(activity);

      $('.ter-weight-alert').hide();
      $('.ter-bmi-alert').hide();
      $('.ter-note').html('');
      $('.ter-note2').hide();
      $('#ter-result').hide();

      if (ter_wt == '' || ter_bmi == '') {
        if (ter_wt == '') {
          $('.ter-weight-alert').show();
        }
        if (ter_bmi == '') {
          $('.ter-bmi-alert').show();
        }
      } else {
        $('#ter-result').css({ display: 'inline-block' });

        if (activity == 'sedentary') {
          act_rate = 30;
        } else if (activity == 'light') {
          act_rate = 35;
        } else if (activity == 'moderate') {
          act_rate = 40;
        } else if (activity == 'active') {
          act_rate = 45;
        } else {
        }

        if (ter_wt_type == 'lbs') {
          var ter = ter_wt_lbs * act_rate;
        } else {
          var ter = ter_wt * act_rate;
        }

        var ter = ter_wt * act_rate;

        // Underweight
        if (ter_bmi < 18.5) {
          ter += 500;
        }
        // Normal weight
        else if (ter_bmi >= 18.5 && ter_bmi <= 24.9) {
          // Nothing to do here... normal weight :P
        }
        // Overweight or Pre-Obese
        else if (ter_bmi >= 25 && ter_bmi <= 29.9) {
          ter -= 500;
        }
        // Obese
        else if (ter_bmi >= 30) {
          ter -= 1000;
        }

        ter = Math.round(ter / 100) * 100;

        $('.ter-result').show().html(ter);

        // var link = 'uploads/pdf/';
        var link = '' + 'uploads/pdf/';

        if (ter >= 1000 && ter < 1100) {
          link += '1000.pdf';
          $('.ter-note2').show();
        } else if (ter >= 1100 && ter < 1200) {
          link += '1100.pdf';
          $('.ter-note2').show();
        } else if (ter >= 1200 && ter < 1300) {
          link += '1200.pdf';
          $('.ter-note2').show();
        } else if (ter >= 1300 && ter < 1400) {
          link += '1300.pdf';
          $('.ter-note2').show();
        } else if (ter >= 1400 && ter < 1500) {
          link += '1400.pdf';
          $('.ter-note2').show();
        } else if (ter >= 1500 && ter < 1600) {
          link += '1500.pdf';
          $('.ter-note2').show();
        } else if (ter >= 1600 && ter < 1700) {
          link += '1600.pdf';
          $('.ter-note2').show();
        } else if (ter >= 1700 && ter < 1800) {
          link += '1700.pdf';
          $('.ter-note2').show();
        } else if (ter >= 1800 && ter < 1900) {
          link += '1800.pdf';
          $('.ter-note2').show();
        } else if (ter >= 1900 && ter < 2000) {
          link += '1900.pdf';
          $('.ter-note2').show();
        } else if (ter >= 2000 && ter < 2100) {
          link += '2000.pdf';
          $('.ter-note2').show();
        } else if (ter >= 2100 && ter < 2200) {
          link += '2100.pdf';
          $('.ter-note2').show();
        } else if (ter >= 2200 && ter < 2300) {
          link += '2200.pdf';
          $('.ter-note2').show();
        } else if (ter >= 2300 && ter < 2400) {
          link += '2300.pdf';
          $('.ter-note2').show();
        } else if (ter >= 2400 && ter < 2500) {
          link += '2400.pdf';
          $('.ter-note2').show();
        } else if (ter >= 2500 && ter < 2600) {
          link += '2500.pdf';
          $('.ter-note2').show();
        } else if (ter >= 2600 && ter < 2700) {
          link += '2600.pdf';
          $('.ter-note2').show();
        } else if (ter >= 2700 && ter < 2800) {
          link += '2700.pdf';
          $('.ter-note2').show();
        } else if (ter >= 2800 && ter < 2900) {
          link += '2800.pdf';
          $('.ter-note2').show();
        } else if (ter >= 2900 && ter < 3000) {
          link += '2900.pdf';
          $('.ter-note2').show();
        } else if (ter >= 3000 && ter < 3100) {
          link += '3000.pdf';
          $('.ter-note2').show();
        } else if (ter >= 3100 && ter < 3200) {
          link += '3100.pdf';
          $('.ter-note2').show();
        } else if (ter >= 3200 && ter < 3300) {
          link += '3200.pdf';
          $('.ter-note2').show();
        } else if (ter >= 3300 && ter < 3400) {
          link += '3300.pdf';
          $('.ter-note2').show();
        } else if (ter >= 3400 && ter < 3500) {
          link += '3400.pdf';
          $('.ter-note2').show();
        } else if (ter >= 3500 && ter < 3600) {
          link += '3500.pdf';
          $('.ter-note2').show();
        } else if (ter >= 3600 && ter < 3700) {
          link += '3600.pdf';
          $('.ter-note2').show();
        } else if (ter >= 3700) {
          link += '3700.pdf';
          $('.ter-note2').show();
        } else {
        }

        // console.log(link);

        if (ter_bmi < 18.5) {
          $('.ter-note').html(
            '<p>You are below your ideal weight. You can follow this one day sample menu coupled with atleast 30 minutes of exercise 3-4 times a week to GAIN atleast 1 pound (lb) per week.</p>'
          );
        } else if (ter_bmi >= 18.5 && ter_bmi <= 24.9) {
          $('.ter-note').html(
            '<p>Congratulations! You are within your ideal weight. You can follow this one day sample menu coupled with atleast 30 minutes of exercise 3-4 times a week to MAINTAIN a healthy weight.</p>'
          );
        } else {
          $('.ter-note').html(
            '<p>You are above your ideal weight. You can follow this one day sample menu coupled with atleast 30 minutes of exercise 3-4 times a week to LOSE atleast 1 pound (lb) per week.</p>'
          );
        }

        // $('.plan').html(link);
        $('.plan').attr('href', link);
      }
    }

    $('.btn-bmi').click(function () {
      bmi_calculator();
    });

    $('.btn-ter').click(function () {
      ter_calculator();
    });

    $('.calculator').keypress(function (e) {
      if (e.charCode == 13) {
        $(this).find('.btn-main').click();
      }
    });

    $('.calculator')
      .find('input')
      .keydown(function (e) {
        if (e.keyCode == 38 || e.keyCode == 40) {
          return false;
        }
      });

    $('.calculator')
      .find('input')
      .keypress(function (e) {
        if (String.fromCharCode(e.charCode) == '-') {
          return false;
        }
      });
  }
}
