import { Component, OnInit } from '@angular/core';
declare var $: any

@Component({
  selector: 'app-news-inner',
  templateUrl: './news-inner.component.html',
  styles: [
  ]
})
export class NewsInnerComponent implements OnInit {

  constructor() { }

  ngOnInit(): void {

      // article slider
      $('.article-slick').slick({
        arrows: true,
        autoplay: false,
        slidesToShow: 1,
        // slidesToScroll: 3,
        infinite: true,
        prevArrow: $('.article-slider').find('.btn-tab-left'),
        nextArrow: $('.article-slider').find('.btn-tab-right'),
        mobileFirst: true,
          responsive: [
        {
          breakpoint: 700,
          settings: {
            slidesToShow: 2
          }
        },
        {
          breakpoint: 1290,
          settings: {
            slidesToShow: 3
          }
        }
      ]
    });

  }

}
