import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HashLocationStrategy, LocationStrategy } from '@angular/common';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { HeaderComponent } from './header/header.component';
import { FooterComponent } from './footer/footer.component';
import { HomeComponent } from './home/home.component';
import { ProductComponent } from './product/product.component';
import { ProductFaqComponent } from './product-faq/product-faq.component';
import { BreadComponent } from './bread/bread.component';
import { BreadInnerComponent } from './bread-inner/bread-inner.component';
import { StoryComponent } from './story/story.component';
import { NewsComponent } from './news/news.component';
import { NewsInnerComponent } from './news-inner/news-inner.component';
import { WellnessArticleComponent } from './wellness-article/wellness-article.component';
import { WellnessCalendarComponent } from './wellness-calendar/wellness-calendar.component';
import { WellnessEventComponent } from './wellness-event/wellness-event.component';
import { WellnessTeamComponent } from './wellness-team/wellness-team.component';
import { CareersComponent } from './careers/careers.component';
import { ContactComponent } from './contact/contact.component';
import { PlantComponent } from './plant/plant.component';
import { ServiceWorkerModule } from '@angular/service-worker';
import { environment } from '../environments/environment';

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    FooterComponent,
    HomeComponent,
    ProductComponent,
    ProductFaqComponent,
    BreadComponent,
    BreadInnerComponent,
    StoryComponent,
    NewsComponent,
    NewsInnerComponent,
    WellnessArticleComponent,
    WellnessCalendarComponent,
    WellnessEventComponent,
    WellnessTeamComponent,
    CareersComponent,
    ContactComponent,
    PlantComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    ServiceWorkerModule.register('ngsw-worker.js', { enabled: environment.production })
  ],
  providers: [
    {provide: LocationStrategy, useClass: HashLocationStrategy}
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
