import { Component, OnInit } from '@angular/core';
declare var $: any
declare var enquire: any

@Component({
  selector: 'app-bread',
  templateUrl: './bread.component.html',
  styles: [
  ]
})
export class BreadComponent implements OnInit {

  constructor() { }

  ngOnInit(): void {

    // bread treats masonry
    function masonry_load() {
      var $grid = $('.bread-treats').masonry({
        itemSelector: '.recipe-wrap',
        columnWidth: '.recipe-wrap',
        gutter: 10
      });
      var $grid = $('.bread-treats').imagesLoaded( function() {
      // init Masonry after all images have loaded
      $grid.masonry({
        itemSelector: '.recipe-wrap',
        columnWidth: '.recipe-wrap',
        gutter: 10
      });
    });
    }

      enquire.register("screen and (min-width: 700px)", {
          setup : function() {
          // Load in content via AJAX (just the once)
        },
          match : function() {
            masonry_load();
          },
          unmatch : function() {
              $('.bread-treats').masonry('destroy');
          }
      });

            // tab nav slider
      // products-tab
      $('.products-page').find('.tab').slick({
        arrows: true,
        autoplay: false,
        slidesToShow: 1,
        // slidesToScroll: 3,
        infinite: true,
        prevArrow: $('.tab-cont').find('.btn-tab-left'),
        nextArrow: $('.tab-cont').find('.btn-tab-right'),
        mobileFirst: true,
          responsive: [
        {
          breakpoint: 500,
          settings: {
            slidesToShow: 2
          }
        },
        {
          breakpoint: 700,
          settings: {
            slidesToShow: 3
          }
        },
        {
          breakpoint: 1000,
          settings: {
            slidesToShow: 4
          }
        },
        {
          breakpoint: 1290,
          settings: {
            slidesToShow: 6
          }
        }
      ]
    });

  // bread-tab
    $('.bread-page').find('.tab').slick({
        arrows: true,
        autoplay: false,
        slidesToShow: 1,
        // slidesToScroll: 3,
        infinite: true,
        prevArrow: $('.tab-cont').find('.btn-tab-left'),
        nextArrow: $('.tab-cont').find('.btn-tab-right'),
        mobileFirst: true,
          responsive: [
        {
          breakpoint: 500,
          settings: {
            slidesToShow: 2
          }
        },
        {
          breakpoint: 700,
          settings: {
            slidesToShow: 3
          }
        },
        {
          breakpoint: 1000,
          settings: {
            slidesToShow: 4
          }
        },
        {
          breakpoint: 1290,
          settings: {
            slidesToShow: 5
          }
        }
      ]
    });

  }

}
